import React from "react";
import $ from "jquery";

function EnlargedCard(props) {
  const saveBlog = (e) => {
    $("body").removeClass("editing");
    $("#h1").html(`Title: ` + $(".title-update").val());
    $("#h2").html(`Author: ` + $(".author-update").val());
    $("#h3").html(`Isbn: ` + $(".isbn-update").val());
  };
  const editBlog = (e) => {
    console.log("editfycj");
    $("body").addClass("editing");
    var title = e.target.parentElement
      .querySelector("#h1")
      .innerHTML.substring(7);
    var author = e.target.parentElement
      .querySelector("#h2")
      .innerHTML.substring(8);
    var isbn = e.target.parentElement.querySelector("#h3").innerHTML.substring(5);
    console.log(props.title);

    $("#h1").html(
      '<input type="text" class="form-control title-update" value="' +
        title +
        '">'
    );

    $("#h2").html(
      '<input type="text" class="form-control author-update" value="' +
        author +
        '">'
    );

    $("#h3").html(
        '<input type="text" class="form-control isbn-update" value="' +
        isbn +
        '">'
    );
  };
  return (
    <React.Fragment>
      <div className="container mt-4">
        <h1 id="heading" className="display-4 text-center">
          <i class="fas fa-bolt text-primary"></i> My
          <span class="text-primary">Book</span>List
        </h1>
      </div>
      <div id="cards" className="container mt-4">
        <div className="card">
          <div className="card-header">
            <img src={props.url} id="img4"></img>
          </div>
          <div className="card-body">
            <h1 id="h1">Title: {props.title} </h1>
            <h1 id="h2">Author: {props.author} </h1>
           
            <h1 id="h3">Isbn: {props.isbn}</h1>
          </div>
        </div>
        <center><button className="btn btn-info btn-lg" id="EDIT" onClick={editBlog}>
          Edit
        </button></center>
       <center> <button className="btn btn-success btn-lg" id="SAVE" onClick={saveBlog}>
          Save
        </button></center>
      </div>
    </React.Fragment>
  );
}

export default EnlargedCard;