import $ from "jquery";

function Home()
{
   
    return(
       
         <div> 
         <center>
         <h1 className="display-4 text-center">
          <i className="fas fa-book-open text-primary">

          </i>My<span className="text-primary">Book</span>List</h1>
          </center>
          <br/>
          <center>
          <Link to="/form">
        
              <button className="btn btn-primary"  id="ADDBOOK">ADD BOOK</button>
          </Link>
          </center></div>
    );
}
export default Home;