import React,{useState} from "react";
import { Link } from "react-router-dom";
import $ from "jquery";
import Card from './Card';
import VisibilityHook from "./VisibilityHook";
import { toast } from "react-toastify";
var rj=[];
function Bookform()
{
  

  const[title,setTitle]=useState("");
  const[author,setAuthor]=useState("");
  const[isbn,setIsbn]=useState("");
  const[url,setUrl]=useState("");


  const toggle=(e)=>
  {
    $("#btn1").hide();
    $("#btn2").show();
   
  }
  const toggle1=(e)=>
  {
    $("#btn2").hide();
    $("#btn1").show();
  }
  const addData=(e)=>
  {

    e.preventDefault();
    setTitle($('#title').val());
    setAuthor($('#author').val());
    setIsbn($('#isbn').val());
    setUrl($('#url').val());
  

  if (
    $("#title").val() === "" ||
    $("#author").val() === "" ||
    $("#isbn").val() === ""
  ) {

  toast('PLease fill in all details');
  }
  else
  {
  
  
    rj.push({
      title: $("#title").val(),
      author: $("#author").val(),
      isbn: $("#isbn").val(),
      url: $("#url").val().toString(),
    });
    console.log(rj);
    $("#title").val("");
    $("#author").val("");
    $("#isbn").val("");
    $("#url").val("");
 toast("Book Added successfully");
  }
}
 
const[Cardcomponent,toggleCard]=VisibilityHook(<div>
  {console.log(rj)} 
  <Card arr={rj}/>
  <center><button id="btn2" className="btn btn-primary"  onClick={()=>{
    toggleCard();toggleCard2();toggle1();}}>Add New Book</button> </center>
  </div>,false);



  const [formComponent, toggleCard2] = VisibilityHook(

  <div className="one">
    <div className="container mt-4">
       
      <h1 id="heading" className="display-4 text-center">
        <i className="fas fa-bolt text-primary"></i> My
        <span className="text-primary">Book</span>List
      </h1>
    <div id="topcontainer">
      <form className="book-form">
        <div className="form-group">
          <label for="title">Title</label>
          <input type="text" id="title" className="form-control" 
            placeholder="Enter title."/>
        </div>
        <div className="form-group">
          <label for="author">Author</label>
  s        <input type="text" id="author" className="form-control" 
            placeholder="Enter author."/>
        </div>
        <div className="form-group">
          <label for="isbn">Isbn</label>

          <input type="text" id="isbn"className="form-control" 
            placeholder="Enter isbn."/>
        </div>
        <div className="form-group">
          <label for="url">Image URL</label>
          <input
            type="text"
            id="url"
            placeholder="Enter url."
            className="form-control"
          />
        </div>

        <button
          id="addnew2"
          type="button"
          className="btn btn-primary btn-block add btn-lg"
          onClick={addData} 
        >
          Add Book
        </button>
      <center>
        <Link to="/">
          <button
            id="back"
            type="button"
            className="btn btn-success btn-lg "
           
          >
            Go Back to Homescreen
          </button>
        </Link>
        </center>
      </form>
      </div>

    </div></div>,true);
 
return(
 
    <div>
    <div className="card1">
      {formComponent}
  {Cardcomponent}
  </div>
     <center><button id="btn1" className="btn btn-primary"  onClick={()=>{
     toggleCard();toggleCard2();toggle();}}>View Book</button></center>
   
</div>

 



    
  );
  }


export default Bookform;