import React,{useState,useEffect} from "react";
import Enlarged from "./Enlarged";
import Home from "./Home";
import { Link } from "react-router-dom";
import VisibilityHook from "./VisibilityHook";
import $ from "jquery";
function Card(props) {
  console.log(props.arr);


 var title=props.title;
 var author=props.author;
 var isbn=props.isbn;
 var url=props.url;



 var[Enlargedcomponent,toggleCard3]=VisibilityHook(<div><Enlarged 
 title={title} author={author} isbn={isbn} url={url} /><center><button
 className="btn btn-primary" id="btn3"
 onClick={() => {
   toggleCard3();
   toggleCard4();
 }}
>
 Back to Card
</button>

</center>
  </div>,false);
const renderCard = (card, index) => {
  return (
    <div className="card col-xs-12 col-sm-12 col-md-6 col-lg-3" key={index} >
      <div className="card-header">
        <img src={card.url} id="img3"></img>
      </div>
      <div className="card-body">
        <p>Title: {card.title} </p>
        <p>Author: {card.author} </p>
        <p>Isbn: {card.isbn} </p>
      </div>

      <button
       id="VIEWBOOK1" className="btn btn-primary"
        onClick={() => {
          toggleCard3();
          toggleCard4();
        }}
      >
        View Book
      </button>
    </div>
  );
};
const [card2Component, toggleCard4] = VisibilityHook(
  <React.Fragment>
    <div className="container mt-4">
      <h1 id="heading" className="display-4 text-center">
        <i class="fas fa-bolt text-primary"></i> My
        <span class="text-primary">Book</span>List
      </h1>
    </div>

    <div id="cards" className="container mt-4">
        <div className="row">
     
          {props.arr.map(renderCard)}
          </div>
      </div>
      
    </React.Fragment>,

    true
  );

 return (



  
  
  <React.Fragment>
   
    {Enlargedcomponent}
 
    {card2Component}

   
  </React.Fragment>
 );
}
export default Card;